import React from "react";
import PropTypes from "prop-types";

import st from './style';
import Colors from '../../style/Colors';

class FlagLabel extends React.Component{
	
	constructor(props) {
		super(props);
		this.state = {};
	}
	
	render(){

		const LeftFlag = (props) => <svg  viewBox="0 0 15 48" fill={props.bgColor} xmlns="http://www.w3.org/2000/svg">
			<path d="M14 47V1H1L10 23L1 47H14Z"/>
		</svg>

		const RightFlag = (props) => <svg  viewBox="0 0 15 48" fill={props.bgColor} xmlns="http://www.w3.org/2000/svg">
			<path d="M1 47V1H14L5 23L14 47H1Z" />
		</svg>

		return(
			<div style={st.container(this.props)}>
				<div style={st.div(this.props)}>
					<LeftFlag bgColor={this.props.bgColor}/>
				</div>

				<div style={st.content(this.props)}>
					{this.props.children}
					{this.props.label}
				</div>
				<div style={st.div(this.props)}>
					<RightFlag bgColor={this.props.bgColor}/>
				</div>
			</div>
		)
	}
}

FlagLabel.propTypes = {
	height: PropTypes.number, 
	fullWidth: PropTypes.bool,
	bgColor: PropTypes.string,
	labelColor:PropTypes.string,
	label: PropTypes.string,
	children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.node,
    PropTypes.array,
	]),
};
FlagLabel.defaultProps = {
	height: 48,
	fullWidth: false,
	bgColor: Colors.white,
	labelColor: Colors.pink,
	label: '',
	children: null,
};

export default FlagLabel;